<template>
    <el-table
    :data="tableData"
    stripe
    v-loading="loading"
    style="width: 100%">
    <el-table-column
    prop="date"
    label="日期"
    width="180">
</el-table-column>
<el-table-column
prop="name"
label="姓名"
width="180">
</el-table-column>
<el-table-column
prop="address"
label="地址">
</el-table-column>
</el-table>
</template>

<script>
export default {
    data() {
        return {
            loading : true,
            tableData: []
        }
    },
    created () {
        var that = this;
        setTimeout(function(){
            that.loading = false;
            that.tableData.push({
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
            }, {
                date: '2016-05-04',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1517 弄'
            }, {
                date: '2016-05-01',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1519 弄'
            }, {
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1516 弄'
            })
        },500)
    }
}
</script>

<style>

</style>



